import "./styles.css";
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Grid, Card, Box } from "@material-ui/core";

export default function App() {
  const [mons, setMons] = useState([{ name: "bulbasaur", num: 1 }]);

  const baseUrl = "https://pokeapi.co/api/v2/pokemon/";

  async function getData() {
    let individualPokemonList = axios
      .get(baseUrl)
      .then((x) => {
        // console.log("got resp: ", JSON.stringify(x.data));
        console.log("total pokemon count: ", x.data.count);
        console.log("next page of pokemon: ", x.data.next);
        console.log("num of pokemon in this response: ", x.data.results.length);
        return x.data.results;
      })
      .catch((err) => {
        console.error("failed to get list of pokemon: ", err);
      });

    // TODO: grab individual pokemon data, save and display
  }

  useEffect(() => {
    getData();
  }, []);

  const PokemonItem = (props) => {
    let info = props.data;
    return (
      <Card style={{ flex: 1, width: 400, height: 120 }}>
        <Grid container style={{ flexDirection: "row", padding: 10 }}>
          <Box style={{ marginLeft: 10 }}>id: {info.num}</Box>
          <Box style={{ marginLeft: 10 }}>name: {info.name}</Box>
        </Grid>
      </Card>
    );
  };

  return (
    <div className="App">
      <h1>Pokedex MVP</h1>
      <button style={{ width: 60, height: 28 }} onClick={() => getData()}>
        Refresh
      </button>
      <Grid
        container
        style={{ flexDirection: "row", padding: 10, alignSelf: "center" }}
      >
        {mons.map((x) => {
          return <PokemonItem data={x} />;
        })}
      </Grid>
    </div>
  );
}
